:root {
  --lightgray: #d9d9d9;
  --grey: #99738e;
  --orange: #f64c72;
  --darkGrey: #2f2fa2;
  --caloryCard: #242582;
  --planCard: linear-gradient(210.41deg, #f64c72 1.14%, #eb8c18 100.75%);
  --appColor: #3c3f45;
  --accent: orange;
}
.App {
  display: flex;
  flex-direction: column;
  background-image: url(data:image/png;base64,);
}
::-webkit-scrollbar {
  display: none;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li {
  margin: 0;
  padding: 0;
}
button {
  cursor: pointer;
}
.btn:hover,
.btn:focus {
  background: var(--accent);
  border-color: transparent;
  color: white;
}
img {
  display: block;
}
.section {
  background-color: var(--darkGrey);
  background-image: url(data:image/png;base64,);
  padding: 60px 0;
}

.container {
  width: 1200px;
  display: block;
  padding: 0 24px;
  margin-left: auto;
  margin-right: auto;
}

.title {
  margin-bottom: 50px;

  font-size: 42px;
  font-style: italic;
  text-transform: uppercase;
  text-align: center;
}
.title__span {
  margin: 0 30px;
  color: white;
}
.stroke-text {
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke: 1px rgb(255, 255, 255);
}

.btn-first {
  background-color: var(--orange);
  color: white;
  border: 4px solid var(--orange);
  padding: 10px;
}
.btn-second {
  background-color: transparent;
  color: white;
  border: 4px solid var(--orange);
  padding: 10px;
}
.blur {
  background: rgba(253, 120, 43, 0.7);
  border-radius: 50%;
  position: absolute;
  filter: blur(120px);
  z-index: 0;
}
