.hero {
  background-color: var(--darkGrey);
  background-image: url(data:image/png;base64,);
}
.blur__hero {
  width: 530px;
  height: 530px;
  top: 10%;
  left: 10%;
  filter: blur(280px);
}
.hero__container {
  display: flex;
  justify-content: space-between;
}
.hero__left-side {
  position: relative;

  padding: 2rem 1.5rem;

  flex: 3;
}
.hero__right-side {
  background-color: var(--orange);

  flex: 1;
  position: relative;
}
